.projectsContainer {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 30px;
    padding: 50px 20px 20px;
    max-width: 1200px;
    margin: 0 auto; 
    margin-top: 80px;
}

.projectCard {
    background-color: #1b1b1b;
    border-radius: 16px;
    padding: 20px;
    width: 300px; 
    height: 500px; 
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
}

.projectCard:hover {
    transform: translateY(-10px);
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.3);
}

.projectImage {
    width: 100%;
    border-radius: 8px;
    margin-bottom: 15px;
    flex-shrink: 0;
}

.projectTitle {
    font-size: 1.5em;
    color: #ffffff;
    margin-bottom: 10px;
}

.projectDescription {
    font-size: 1em;
    color: #cccccc;
    margin-bottom: 20px;
    flex-grow: 1;
    overflow: hidden; /* Hide overflow to maintain uniform card size */
}

.projectButton {
    display: inline-block;
    background-color: #6c63ff;
    color: white;
    padding: 10px 20px;
    border-radius: 8px;
    text-decoration: none;
    transition: background-color 0.3s ease;
    text-align: center;
    margin-top: auto; /* Ensure the button stays at the bottom */
}

.projectButton:hover {
    background-color: #5a54d2;
}